module.exports = "<h1 id=getting-started>Getting Started</h1> <h2 id=webpack>Webpack</h2> <p>If you are using module bundlers such as Webpack, Rollup, Laravel elixir/mix, etc you may prefer to directly include the package into your project. To get started, use yarn or npm to get latest version of bootstrap-vue and bootstrap 4:</p> <pre class=hljs><span class=hljs-comment># With NPM:</span>\nnpm i bootstrap-vue bootstrap@4.0.0-beta.2\n\n<span class=hljs-comment># With Yarn:</span>\nyarn add bootstrap-vue bootstrap@4.0.0-beta.2\n</pre> <p>Then, register BootstrapVue plugin in your app entry point:</p> <pre class=hljs><span class=hljs-keyword>import</span> Vue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;vue&apos;</span>\n<span class=hljs-keyword>import</span> BootstrapVue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue&apos;</span>\n\nVue.use(BootstrapVue);\n</pre> <p>And import css files from both Bootstrap 4 &amp; Bootstrap-Vue:</p> <p><strong>Note</strong>: <em>requires webpack configuration to load css files (<a href=https://webpack.js.org/guides/asset-management/#loading-css>official guide</a>)</em></p> <pre class=hljs><span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap/dist/css/bootstrap.css&apos;</span>\n<span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap-vue/dist/bootstrap-vue.css&apos;</span>\n</pre> <p><strong>Note:</strong> If you are unable or do not want to add additional packages to dev dependency, you have to manually include both <a href=https://v4-alpha.getbootstrap.com/getting-started/download/ >Bootstrap</a> and <a href=https://unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.css>BootstrapVue</a> CSS files in your bundle or reference them from <code>static/</code> via <code>index.html</code>.</p> <h2 id=nuxt-js>Nuxt.js</h2> <p>Install dependencies:</p> <pre class=hljs><span class=hljs-comment># With NPM:</span>\nnpm i bootstrap-vue bootstrap@4.0.0-beta.2\n\n<span class=hljs-comment># With Yarn:</span>\nyarn add bootstrap-vue bootstrap@4.0.0-beta.2\n</pre> <p>Add <code>bootstrap-vue/nuxt</code> to modules section of <strong>nuxt.config.js</strong></p> <pre class=hljs>{\n  <span class=hljs-attr>modules</span>: [\n    <span class=hljs-string>&apos;bootstrap-vue/nuxt&apos;</span>,\n\n    <span class=hljs-comment>// Or if you have custom bootstrap CSS...</span>\n    [<span class=hljs-string>&apos;bootstrap-vue/nuxt&apos;</span>, { <span class=hljs-attr>css</span>: <span class=hljs-literal>false</span> }],\n  ]\n}\n</pre> <h2 id=vue-cli>vue-cli</h2> <p>Bootstrap-Vue has two vue-cli templates available:</p> <ul> <li><a href=https://github.com/bootstrap-vue/webpack-simple>webpack-simple</a>: Quick scaffold for a proof of concept or small app</li> <li><a href=https://github.com/bootstrap-vue/webpack>webpack</a>: Larger, production ready template with more options</li> </ul> <pre class=hljs><span class=hljs-comment># Ensure vue-cli is installed and up to date</span>\nnpm i -g vue-cli\n<span class=hljs-comment># Initialize a bootstrap project in the directory &apos;my-project&apos;</span>\nvue init bootstrap-vue/webpack-simple my-project\n<span class=hljs-comment># Change into the directory</span>\n<span class=hljs-built_in>cd</span> my-project\n<span class=hljs-comment># Install dependencies</span>\nnpm i\n<span class=hljs-comment># Fire up the dev server with HMR</span>\nnpm run dev\n</pre> <p>You can repeat the commands above replacing <code>bootstrap-vue/webpack-simple</code> with <code>bootstrap-vue/webpack</code> for the webpack template.</p> <h2 id=individual-components-and-directives>Individual components and directives</h2> <p>If you would like to only pull in a specific component or set of components, you can do this by directly importing those components.</p> <p>To cherry pick a component/directive, start by importing it in the file where it is being used:</p> <pre class=hljs><span class=hljs-keyword>import</span> bModal <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/es/components/modal/modal&apos;</span>\n<span class=hljs-keyword>import</span> bModalDirective <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/es/directives/modal/modal&apos;</span>\n</pre> <p>Then add it to your component definition:</p> <pre class=hljs>Vue.component(<span class=hljs-string>&apos;my-component&apos;</span>, {\n    <span class=hljs-attr>components</span>: {\n        <span class=hljs-string>&apos;b-modal&apos;</span>: bModal\n    },\n    <span class=hljs-attr>directives</span>: {\n        <span class=hljs-string>&apos;b-modal&apos;</span>: bModalDirective\n    }\n    <span class=hljs-comment>// ...</span>\n})\n</pre> <p>Or register them globally:</p> <pre class=hljs>Vue.component(<span class=hljs-string>&apos;b-modal&apos;</span>, bModal);\nVue.directive(<span class=hljs-string>&apos;b-modal&apos;</span>, bModalDirective);\n</pre> <p>Vue and ES2015 allow for various syntaxes here, so feel free to utilize kebab-casing (shown), camel-casing, pascal-casing, and/or object property shorthand.</p> <h3 id=component-groups-and-directives-as-vue-plugins>Component groups and Directives as Vue plugins</h3> <p>You can also import component groups and directives as Vue plugins by importing the component group or directive directory:</p> <pre class=hljs><span class=hljs-comment>// This imports &lt;b-modal&gt; as well as the v-b-modal directive as a plugin:</span>\n<span class=hljs-keyword>import</span> { Modal } <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/es/components&apos;</span>;\nVue.use(Modal);\n\n<span class=hljs-comment>// This imports &lt;b-card&gt; along with all the &lt;b-card-*&gt; sub-components as a plugin:</span>\n<span class=hljs-keyword>import</span> { Card } <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/es/components&apos;</span>;\nVue.use(Card);\n\n<span class=hljs-comment>// This imports directive v-b-scrollspy as a plugin:</span>\n<span class=hljs-keyword>import</span> { Scrollspy } <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/es/directives&apos;</span>;\nVue.use(Scrollspy);\n</pre> <p>When importing as plugins, all subcomponents and related directives are imported in most cases. i.e. When importing <code>&lt;b-nav&gt;</code>, all the <code>&lt;nav-*&gt;</code> sub components are also included, as well all dropdown sub components.</p> <p>Refer to the component and directive documentation for details.</p> <h3 id=webpack-babel>Webpack + Babel</h3> <p>When importing components/directives individually, you must configure your app to properly build the bootstrap-vue library source code. This commonly involves white-listing the node module for your babel loader rule in webpack.</p> <pre class=hljs><span class=hljs-comment>// webpack.config.js</span>\n<span class=hljs-keyword>const</span> webpack = <span class=hljs-built_in>require</span>(<span class=hljs-string>&apos;webpack&apos;</span>)\n<span class=hljs-keyword>const</span> path = <span class=hljs-built_in>require</span>(<span class=hljs-string>&apos;path&apos;</span>)\n\n<span class=hljs-built_in>module</span>.exports = {\n    <span class=hljs-attr>entry</span>: <span class=hljs-string>&apos;./app.js&apos;</span>,\n    <span class=hljs-attr>output</span>: {\n        <span class=hljs-attr>filename</span>: <span class=hljs-string>&apos;bundle.js&apos;</span>\n    },\n    <span class=hljs-attr>module</span>: {\n        <span class=hljs-attr>rules</span>: [\n            {\n                <span class=hljs-attr>test</span>: <span class=hljs-regexp>/\\.js$/</span>,\n                <span class=hljs-attr>include</span>: [ <span class=hljs-comment>// use `include` vs `exclude` to white-list vs black-list</span>\n                    path.resolve(__dirname, <span class=hljs-string>&quot;src&quot;</span>), <span class=hljs-comment>// white-list your app source files</span>\n                    <span class=hljs-built_in>require</span>.resolve(<span class=hljs-string>&quot;bootstrap-vue&quot;</span>), <span class=hljs-comment>// white-list bootstrap-vue</span>\n                ],\n                <span class=hljs-attr>loader</span>: <span class=hljs-string>&quot;babel-loader&quot;</span>\n            }\n        ]\n    }\n}\n</pre> <h2 id=browser>Browser</h2> <pre class=hljs><span class=hljs-comment>&lt;!-- Add this to &lt;head&gt; --&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>link</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;text/css&quot;</span> <span class=hljs-attr>rel</span>=<span class=hljs-string>&quot;stylesheet&quot;</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap@next/dist/css/bootstrap.min.css&quot;</span>/&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>link</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;text/css&quot;</span> <span class=hljs-attr>rel</span>=<span class=hljs-string>&quot;stylesheet&quot;</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.css&quot;</span>/&gt;</span>\n\n<span class=hljs-comment>&lt;!-- Add this after vue.js --&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span> <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;//unpkg.com/babel-polyfill@latest/dist/polyfill.min.js&quot;</span>&gt;</span><span class=\"undefined\"></span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span> <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.js&quot;</span>&gt;</span><span class=\"undefined\"></span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n</pre> <h2 id=build-variants>Build variants</h2> <p>Choosing the best variant for your build environment / packager helps less bundle sizes. If your bundler supports es modules, it will automatically prefer it over commonjs.</p> <table class=\"table b-table table-sm table-striped\"> <thead class=thead-default> <tr> <th>Variant</th> <th>Environments</th> <th>Package path</th> </tr> </thead> <tbody> <tr> <td><strong>ES Module</strong></td> <td>Webpack 2 / Rollup</td> <td><code>dist/bootstrap-vue.esm.js</code></td> </tr> <tr> <td>commonjs2</td> <td>Webpack 1 / ...</td> <td><code>dist/bootstrap-vue.common.js</code></td> </tr> <tr> <td>UMD</td> <td>Browser</td> <td><code>dist/bootstrap-vue.js</code></td> </tr> </tbody> </table> <h2 id=migrating-a-project-already-using-bootstrap>Migrating a project already using Bootstrap</h2> <p>If you&apos;ve already been using Bootstrap 4, there are a couple adjustments you may need to make to your project:</p> <ul> <li>Remove the bootstrap.js file from your page scripts or build pipeline</li> <li>If Bootstrap is the only thing relying on jQuery, you can safely remove it &#x2014; BootstrapVue <strong>does not</strong> depend on jQuery</li> <li>Don&apos;t forget to include the <code>bootstrap-vue.css</code> file!</li> <li>Convert your native bootstrap HTML markup into the simplified Bootstrap-Vue custom component markup</li> </ul> <h2 id=browsers-support>Browsers Support</h2> <h3 id=css>CSS</h3> <p>BootstrapVue is to be used with Bootstrap 4 CSS. Please see <a href=https://v4-alpha.getbootstrap.com/getting-started/browsers-devices>Browsers and devices</a> for more information about browsers currently supported by Bootstrap 4.</p> <h3 id=js>JS</h3> <p>BootstrapVue is written in Vue! So this is up to your project and bundler which browsers are supported. If you want to support older IE, Android and IOS devices, you may want to use <a href=https://babeljs.io/docs/usage/polyfill>Babel Polyfill</a></p> <h3 id=ie-11>IE 11</h3> <p>You&apos;ll need babel-polyfill for BootstrapVue to work properly. In order to support this browser:</p> <ul> <li><code>npm install babel-polyfill</code></li> <li>Import it in your app main entry point with <code>import &apos;babel-polyfill&apos;</code></li> </ul> <h2 id=tooling-support>Tooling Support</h2> <h3 id=vscode-vetur>vscode + vetur</h3> <p>If you are using <a href=https://code.visualstudio.com/ >vscode</a> as your text editor, bootstrap-vue has intellisense autocompletion for component attributes available when using the <a href=\"https://marketplace.visualstudio.com/items?itemName=octref.vetur\">vetur extension</a>.</p> <p><a href=https://twitter.com/AlexSashaRegan/status/912769997776158723>Twitter: vetur + bootstrap-vue</a></p> ";

